body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
}
* {
  box-sizing: border-box;
}
html {
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
}
::-webkit-scrollbar {
  width: 4px;
  background: rgba(0, 0, 0, 0);
}
/* Track */
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #656565;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  width: 5px;
  background: #000000;
  border-radius: 10px;
}

::-moz-selection {
  /* Code for Firefox */
  color: #57bdeb;
  background: rgb(72, 72, 72);
}

::selection {
  color: white;
  background: #3d3d3d;
}
ul {
  list-style: none;
  padding: 0;
}
.app {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #cfe1ef;
  max-width: 768px;
  width: 768px;
  border-radius: 20px;
  overflow-x: hidden;
}
.navbar {
  background-color: #d5ecec;
  height: 76px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: #131313;
  font-weight: bold;
  padding: 0 50px;
}
.navbar .logo {
  font-size: 20px;
}
.navbar button {
  border: 0;
  background-color: #00000000;
  color: rgb(134, 41, 41);
}
.messages {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 500px;
  margin: 0;
}
.msg {
  display: grid;
  grid-template-columns: 20% 80%;
  margin: 20px 0;
}
.msg .img img {
  border-radius: 50%;
}
.msg .img {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header {
  display: flex;
  flex-direction: row;
}
.header .name {
  color: #131313;
  font-weight: bold;
}
.header .date {
  color: rgb(124, 124, 124);
  margin-left: 20px;
}
.content {
  color: rgb(49, 49, 49);
  font-size: 19px;
}
.channel {
}
.inpt-msg {
  width: 90%;
  height: 51px;
  border-radius: 15px;
}
.send-btn {
  background-color: #00000000;
  border: 0;
}
.form {
  padding: 0 20px 20px 20px;
}
.in-from {
  background-color: #ffffffec;
  border-radius: 40px;
  height: 76px;
  display: grid;
  grid-template-columns: 80% 20%;
  align-items: center;
  justify-items: center;
  overflow: hidden;
  padding: 0 10px;
}
@media screen and (max-width: 768px) {
  .app {
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 10% 90%;
  }
  .channel {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 90% 10%;
    padding-bottom: 20px;
  }
  .navbar {
    width: 100%;
  }
  .messages {
    height: 100%;
  }
}
